*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

.free{
    padding: 10px 30px ;
    width: 100%;
    background: #010f1c;
 
    display: flex;
}
.free .icon{
    color: #f3b123;
    font-size: 32px;

}
.free p{
    font-size: 10px;
    color: white;
    margin-top: 9px;
    margin-left: 5px;
    font-size: 16px;
}
.main-header {
    padding: 30px 40px;
    width: 100%;
    border-radius: 30px;
    border-bottom: 1px solid black;
}
.main-header .container{
    
    display: flex;
    justify-content: space-between;
}

.main-header .container .logo img{
   width: 40%;
}
.main-header .container .search-box{
    margin-right: 30px;
    margin-top: 30px;
}
.main-header .container .search-box  input{
    outline: none;
    padding: 20px 30px ;
    
    border: 1px solid #0980ff;
    width: 300px;
    color: #151616;
}

.main-header .container .search-box button{
    border: 3px solid #0980ff;
    outline: none;
    padding: 18px 30px;
    cursor: pointer;
    background:#0980ff;
    color: white;
    
}
.main-header .container .icon{
    display: flex;
    margin-right: 30px;
}
.main-header .container .icon .account {
    text-align: center;
    margin-right: 30px;
}
.main-header .container .icon .account .user-icon{
    margin-right: 10px;
    font-size: 22px;
    color: black;
    cursor: pointer;
}
.main-header .container .icon .account p{
  justify-content:space-between ;
    margin-right: 5px;
    margin-top: 20px;

}
.main-header .container .icon .second-icon{
    display: flex;
     

}
.main-header .container .icon .second-icon .link{
    margin-right: 5px;
    font-size: 22px;
    cursor: pointer;
    color: blue;
}

.header{
    width: 100%;
    padding: 20px 30px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
}
.header .container{
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}

.header .container .nav ul{
    display: flex;

}
.header .container .nav ul li{
    margin-left: 20px;
    list-style: none;
}

.header .container .nav ul li .link{
    text-decoration: none;
    color: #010f1c;
    transition: 0.5s
}
.header .container .nav ul li .link:hover{
    color: #0980ff;
}

.header .container .auth button{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
    justify-content: space-between ;
    outline: none;
    border: none;
    background: none;
}

