*
{
 margin: 0;
 padding: 0;
 font-family: sans-serif;
 box-sizing: border-box;   
}
.products{
    padding: 30px 40px;
    width: 100%;
}

.products h3{
    font-size: 42px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 300;
    color: #010f1c;
}
.products p{
    margin-top: 10px;
    color: #9a9a9a;

}

.products .container{
    margin-top: 20px;
    max-width: 100%;
    display: flex ;
    justify-content: space-betweens;
}

.products .container .filter {
    padding: 20px 30px;

}
.products .container .filter .categories h3{
    font-size: 16px;
    color: #010f1c;
    text-transform: uppercase;
    font-weight: 600;
}

.products .container .filter .categories ul{
    margin-top: 10px;
}

.products .container .filter .categories ul li{
    margin-top: 30px;
    list-style: none;
    color: #010f1c;
    cursor: pointer;
}

.products .container .productbox{
    margin-right: 0;
    
}
.products .container .productbox .content {
  
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}
.products .container .productbox .content .box{
    outline: none;
    padding: 10px 20px;
    overflow: hidden;
     width: 30%;
     height: 20%;
     background: #eaebec;
    margin-bottom: 15px;
    border-radius: 10px;   
    margin-right: 20px;
    margin-left: 30px;
}
.products .container .productbox .content .box:hover{
    box-shadow: rgba(0, 0,0, 0.35) 0px 5px 15px;
}
.products .container .productbox .content .box .img-box{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px  solid #e2e0e0;
}
.products .container .productbox .content .box .img-box img{
    width: 200px;
    height: 200px;
    margin-left: 30px;
    margin-right: -8%;
}
.products .container .productbox .content .box .img-box .icon {
    z-index: 1;
    position: relative;
   margin-right: 20px;
   margin-left: 100px;
   display: flex;
   flex-direction: column; 
    margin-top: 20px;
   transition: 0.5s;
}
.products .container .productbox .content .box .img-box .icon li{
    list-style: none;
    padding: 10px 10px;
    border-bottom: 1px solid #9a9b9a;
    color: #010f1c;
    cursor:pointer ;
    background: #fff;
    box-shadow:  rgba(0,0,0,0.24) 0px 3px 8px;
    transition: 0.5s;
}
.products .container .productbox .content .box .img-box .icon li:hover{
    background: #0989ff;

}
.products .container .productbox .content .box .details{
    margin-top: 10px;
}
.products .container .productbox .content .box .details p{
    color: #9a9a9a;
    font-size: 14px;
}
.products .container .productbox .content .box .details h3{
    font-size: 14px;
    margin-top: 10px;
    color: #010f1c;
    transition: 0.5s;
}
.products .container .productbox .content .box:hover .details h3{
    color: #0989ff;
}

.products .container .productbox .content .box .details h4{
    margin-top: 5px;
    font-size: 14px;
    color: #0989ff;

}


.product-details{
    margin-top: 30px;
    padding: 30px 40px;
    width: 100%;
    height: 120%;
    position: fixed;
    z-index: 1;
    margin-top: -20%;
    background: rgba(0, 0,0, 0.24);
}
.product-details .container {
    margin-top: 5%;
    margin-left: 21%;
    width: 1000px;
    height: 550px;
    background: #fff;
    padding: 20px 30px;
    border-radius: 10px;
    box-shadow: rgba(0, 0,0, 0.24)0px 3px 8px;
}
.product-details .container button{
    margin-left: 100%;
    border: none;
    outline: none;
    background: none;
    font-size: 22px;
    cursor: pointer;
}
.product-details .container .productbox{
    max-width: 100%;
    display: flex;
    padding: 20px 30px;
    margin-top: 20px;
}
.product-details .container .productbox .img-box{
    padding: 20px;
    width: 400px;
    height: 400px;
    background: #f1f1f1;
}
.product-details .container .productbox .img-box img{
    width: 250px;
    height: 350px;
}
.product-details .container .productbox .details{
    margin-left: 30px;
    margin-top: 10px;
}
.product-details .container .productbox .details h4{
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 200;
    color: #9a9a9a;
    letter-spacing: 1px;
}
.product-details .container .productbox .details h2 {
    margin-top: 30px;
    font-size: 32px;
    color: #010f1c;
    text-transform: capitalize;
}
.product-details .container .productbox .details p{
    margin-top: 10px;
    color: #4a4a4a;
}
.product-details .container .productbox .details h3{
    margin-top: 10px;
    color: #0989ff;
    font-size: 22px;
    letter-spacing: 1px;
}
.product-details .container .productbox .details button{
    margin-top: 30px;
    margin-left: -19px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 18px;
    color: #fff;
    background: #010f1c;
    transition: 0.5s;
    border: none;
    outline: none;
}
.product-details .container .productbox .details button:hover{
    background: #0989ff;
}