*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

.top-banner{
    padding: 30px 40px;
    width: 100%;
    background: #e3edf6;
}
.top-banner .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.top-banner .container .details{
    margin-top: 13%;
    margin-left: 30px;
}
.top-banner .container .details h2{
    font-size: 42px;
    color: #010f1c;
    margin-right: 50%;
    margin-bottom: 40px;
}
.top-banner .container .details .Link{
    
    padding: 10px 20px;
    text-decoration:none ;
    border-radius: 5px;
    background: #fff;
}
.top-banner .container .details .Link:hover{
    background: #0989ff;
    cursor: pointer;
    color: #e3edf6;
}
.top-banner .container .img-box img{
    width: 85%;
}

.product-type {
    padding: 20px 30px;

    width: 100%;
}
.product-type .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}

.product-type .container .box{
    padding: 10px 20px;
    margin-left: 10px;

}

.product-type .container .box .img-box{
    height: 200px;
    width:200px;
    border-radius: 50%;
    background:#e3edf6;
    overflow: hidden;
    cursor: pointer;
    
}
.product-type .container .box .img-box img{
    
    height: 100px;
    width: 100px;
    align-items: center;
    max-width: 100%;
    justify-content: center;
    margin-top: 40px;
    margin-left: 50px;
    transition: 0.5s;
}

.product-type .container .box .img-box:hover img{
     
   
    transform: scale(1.2);

}

.product-type .container .box .details{
    max-width: 100%;
    text-align: center;
    align-items: center;
}
.product-type .container .box .details p{
    margin-top: 10px;
    color: #9a9b9a;
}
/*-----------about----------->*/
.about{
    padding-left: 40px 50px;
    width: 100%;

}
.about .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    
}
.about .container .box{
    padding: 10px 20px;
    display: flex;
    background: #e3edf6;
    margin-left: 15px;
    margin-top: 10px;
    height: 90px;
    border-radius: 20px;
    width: 250px;
    cursor: pointer;
}
.about .container .box .icon{
    color: #e44c8c;
    font-size: 32px;
    margin-top: 10px;
    
}

.about .container .box .details{
    margin-top:5px ;
    margin-left: 10px;
}

.about .container .box .details h3{
    color: black;
}

.about .container .box .details p{
    color: #9a9b9a;
}

.product{
    padding: 30px 40px ;
    width: 100%;

}
.product h2{
    font-size: 32px;
    color: #010f1c;
    margin-bottom: 10px;
}

.product .container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.product .container .box {
    outline: none;
    padding: 10px 20px;
    overflow: hidden;
     width: 30%;
     height: 20%;
     background: #eaebec;
    
    margin-bottom: 15px;
    border-radius: 10px;

}

.product .container .box:hover{
    
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
}
.product .container .box .img-box {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px  solid #e2e0e0;

}
.product .container .box .img-box img{
    width: 200px;
    height: 200px;
    margin-left: 30px;
}

.product .container .box .img-box .icon{
    z-index: 1;
    position: relative;
   margin-right: 20px;
   margin-left: 100px;
   display: flex;
   flex-direction: column; 
    margin-top: 20px;
   transition: 0.5s;
}
 
 
 
.product .container .box .img-box .icon li{
    list-style: none;
    padding: 10px 10px;
    border-bottom: 1px solid #9a9b9a;
    color: #010f1c;
    cursor:pointer ;
    background: #fff;
    box-shadow:  rgba(0,0,0,0.24) 0px 3px 8px;
    transition: 0.5s;
}
.product .container .box .img-box .icon li:hover{
    background: #0989ff;
}
.product .container .box   .details{
    margin-top: 10px;
}
.product .container .box  .details p{
    color: #9a9b9a;
    font-size: 14px;
}
.product .container .box .details h3{
    font-size: 14px;
    margin-top: 10px;
    color: #010f1c;
    transition: 0.5s;
}
.product .container .box:hover .details h3{
  color: #0989ff;
}
.product .container .box .details h4{
    margin-top: 5px;
    font-size: 14px;
    color: #0989ff;
}

.banner{
    padding: 20px 30px;
    width: 100%;

}
.banner .container{
    padding: 30px 40px;
    background: #0989ff;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}
.banner .container .details{
    margin-top: 10%;
    margin-left: 20px;
}

.banner .container .details h4{
    color: #e2e0e0;
    font-size: 16px;

}
.banner .container .details h3{
    margin-top: 10px;
    font-size: 42px;
    margin-right: 20%;
    letter-spacing: 1px;
    color: #fff;
}
.banner .container .details p{
    margin-top: 30px;
    color: #fff;
    letter-spacing: 1.5px;
    font-weight: 600;
    margin-bottom: 20px;
}

.banner .container .details .link{
    padding: 10px 30px;
    color: #e2e0e0;
    background: #010f1c;
    text-decoration: none;
    border-radius: 5px;
    transition: 0.5s;
}
.banner .container .details .link:hover{
    color: #010f1c;
    background: #e2e0e0;
}
.banner .container .img-box img{
    width: 80%;
    margin-left: 25%;
}
.product-details{
    margin-top: 30px;
    padding: 30px 40px;
    width: 100%;
    height: 120%;
    position: fixed;
    z-index: 1;
    margin-top: -20%;
    background: rgba(0, 0,0, 0.24);
}
.product-details .container {
    margin-top: 5%;
    margin-left: 21%;
    width: 1000px;
    height: 550px;
    background: #fff;
    padding: 20px 30px;
    border-radius: 10px;
    box-shadow: rgba(0, 0,0, 0.24)0px 3px 8px;
}
.product-details .container button{
    margin-left: 100%;
    border: none;
    outline: none;
    background: none;
    font-size: 22px;
    cursor: pointer;
}
.product-details .container .productbox{
    max-width: 100%;
    display: flex;
    padding: 20px 30px;
    margin-top: 20px;
}
.product-details .container .productbox .img-box{
    padding: 20px;
    width: 400px;
    height: 400px;
    background: #f1f1f1;
}
.product-details .container .productbox .img-box img{
    width: 250px;
    height: 350px;
}
.product-details .container .productbox .details{
    margin-left: 30px;
    margin-top: 10px;
}
.product-details .container .productbox .details h4{
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 200;
    color: #9a9a9a;
    letter-spacing: 1px;
}
.product-details .container .productbox .details h2 {
    margin-top: 30px;
    font-size: 32px;
    color: #010f1c;
    text-transform: capitalize;
}
.product-details .container .productbox .details p{
    margin-top: 10px;
    color: #4a4a4a;
}
.product-details .container .productbox .details h3{
    margin-top: 10px;
    color: #0989ff;
    font-size: 22px;
    letter-spacing: 1px;
}
.product-details .container .productbox .details button{
    margin-top: 30px;
    margin-left: -19px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 18px;
    color: #fff;
    background: #010f1c;
    transition: 0.5s;
    border: none;
    outline: none;
}
.product-details .container .productbox .details button:hover{
    background: #0989ff;
}